<template>
  <div class="hello">
    <Menu />

    <section class="container" v-if="!isSearch">
      <Masonry :item="newses" :more="true" :moreItems="12" @onClick="goLink" />
    </section>

    <section class="container" v-if="isSearch">
      <Masonry :item="search" @onClick="goLink" />
    </section>
  </div>
</template>

<script>
import Menu from "@/components/Menu/index.vue";
import Masonry from "@/components/Masonry/index.vue";
export default {
  name: "HomePage",
  components: { Menu, Masonry },
  props: {
    msg: String
  },
  data() {
    return {
      isSearch: false
    };
  },
  computed: {
    newses: function() {
      return this.$store.getters["News/records"];
    },
    search: function() {
      return this.$store.getters["News/search"];
    }
  },
  methods: {
    goLink(opt) {
      this.$router.push({
        name: "Info",
        params: {
          cat: opt.cat,
          key: opt.key
        }
      });
    }
  },
  mounted() {
    let vm = this;
    vm.$root.$emit("isBack", "off");
    vm.$root.$on("searchChange", data => {
      if (data) {
        vm.$store.commit("News/SEARCH", data);
        vm.isSearch = true;
      } else {
        vm.isSearch = false;
      }
    });
  }
};
</script>
