import Fuse from "fuse.js";
import _ from "lodash";
import { SHEET_LINK } from "@/utils/constants";
import { urlParse, urlStringify } from "@/utils/helpers";
import API from "../api/news";

let catObj = { cat: "trending" };
let urlObj = urlParse();
let params = { ...catObj, ...urlObj };

//default state
const initialState = () => ({
  params: params,
  all: [], // all data
  search: [], // data search
  records: [], // data category
  record: {} // data selected
});

/**
 * State object
 * Use: this.$store.Namemodule.key
 */
const state = initialState();

/**
 * Getter functions
 * Use: this.$store.getters['Namemodule/key']
 */
const getters = {
  params: state => state.params,
  all: state => state.all,
  search: state => state.search,
  records: state => state.records,
  record: state => state.record
};

/**
 * Mutations
 * this.$store.commit('Namemodule/key', data)
 */
const mutations = {
  SET(state, data) {
    state.records = data;
    state.all = _.unionBy(state.all, data, "key"); // all data: merge and filter array
  },
  SET_CAT(state, data) {
    state.params = { ...state.params, ...data };
    let urlParams = urlStringify(state.params);
    if (history)
      history.replaceState({}, "", location.pathname + "?" + urlParams);
  },
  FIND(state, data) {
    let result = state.records.find(record => record.key === data);
    state.record = {};
    if (result) {
      state.record = result;
      return true;
    }
    return false;
  },
  SEARCH(state, data) {
    const options = {
      shouldSort: true,
      threshold: 0.5,
      location: 0,
      distance: 50, //Xác định mức độ trùng khớp phải ở vị trí tìm thấy ký tự
      minMatchCharLength: 2,
      findAllMatches: true,
      keys: ["title", "description"]
    };

    const fuse = new Fuse(state.all, options);

    //extend search join 2 char 'Man 'Old
    let results = fuse.search(data);
    if (results && results.length) {
      let output = [];
      for (const it of results) {
        output.push(it.item);
      }
      state.search = output;
    } else {
      state.search = [];
    }
  },
  ADD(state) {
    state.records.push(state.record);
  },
  EDIT(state, data) {
    let records = state.records;
    records.splice(records.indexOf(data), 1);
    state.records = records;
    state.record = data;
  },
  REMOVE(state, data) {
    let records = state.records;
    records.splice(records.indexOf(data), 1);
  },
  CLEAN(state) {
    state.record = {};
  },

  //cài đặt lại state
  RESET(state) {
    const newState = initialState();
    Object.keys(newState).forEach(key => {
      state[key] = newState[key];
    });
  }
};
/**
 * Actions
 * Use: this.$store.dispatch('Namemodule/key', data)
 */
const actions = {
  setup: async ({ commit }, opt) => {
    let result;
    let qp = urlParse(); //queryParams
    let { cat, limit } = qp;
    let opts = opt || {};
    if (opts.cat) {
      cat = opts.cat;
    }
    if (opts.limit) {
      limit = opts.limit;
    }

    try {
      result = await API.GET_LIMIT({
        url: SHEET_LINK[cat || "trending"],
        limit: limit || 50
      });
    } catch (err) {
      return;
    }
    commit("SET", result);
  },
  find: async ({ commit, state }, opt) => {
    commit("FIND", opt.key);
    let qp = urlParse(); //queryParams
    let { cat } = qp;
    let opts = opt || {};
    if (opts.cat) {
      cat = opts.cat;
    }
    if (!state.record.key) {
      let result;
      try {
        result = await API.GET({ url: SHEET_LINK[cat], key: opt.key });
      } catch (err) {
        return;
      }
      state.record = result;

      state.all = _.unionBy(state.all, [result], "key"); // all data: merge union
    }
  },
  filterByCat: async ({ state }, opt) => {
    let qp = urlParse(); //queryParams
    let { cat, limit, offset } = qp;
    let opts = opt || {};
    if (opts.cat) {
      cat = opts.cat;
    }
    if (opts.limit) {
      limit = opts.limit;
    }

    let result = state.all.filter(function(item) {
      return item.cat === cat;
    });

    if (result.length < 1) {
      try {
        result = await API.GET_LIMIT({
          url: SHEET_LINK[cat],
          limit: limit || 50,
          offset: offset || 0
        });
      } catch (err) {
        return;
      }

      state.all = _.unionBy(state.all, result, "key"); // all data: merge and filter array
    }

    state.records = result;
  },
  moreByCat: async ({ state }, opt) => {
    let result;
    let qp = urlParse(); //queryParams
    let { cat, limit, offset } = qp;
    let opts = opt || {};
    if (opts.cat) {
      cat = opts.cat;
    }
    if (opts.limit) {
      limit = opts.limit;
    }
    if (opts.offset) {
      offset = opts.offset;
    }
    try {
      result = await API.GET_LIMIT({
        url: SHEET_LINK[cat],
        limit: limit || 50,
        offset: offset || 0
      });
    } catch (err) {
      return;
    }

    state.all = _.unionBy(state.all, result, "key"); // all data: merge and filter array

    state.records = state.records.concat(result); // merge array
  },
  add: async ({ commit }) => {
    commit("ADD");
  },
  edit: async ({ commit }, data) => {
    commit("EDIT", data);
  },
  remove: async ({ commit }, data) => {
    commit("REMOVE", data);
  },
  clean: async ({ commit }) => {
    commit("CLEAN");
  },
  reset: async ({ commit }) => {
    commit("RESET");
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
