<template>
  <header id="header">
    <section class="cover container">
      <a id="back" title="back" @click="backLink" v-if="isBack"></a>
      <a href="/" id="logo">
        <img src="/img/icons/logo.png" />
        <span>Xu Hướng</span>
      </a>
      <Search />
    </section>
  </header>
</template>

<script>
import Search from "@/components/Search/index.vue";
export default {
  name: "Header",
  components: { Search },
  data() {
    return {
      isBack: false
    };
  },
  methods: {
    backLink() {
      this.$router.back();
      this.isBack = false;
    }
  },
  mounted() {
    let vm = this;
    vm.$root.$on("isBack", function(val) {
      if (val) {
        vm.isBack = false;
      } else {
        vm.isBack = true;
      }
    });
  }
};
</script>

