<template>
  <div class="masonry-wrap text-center">
    <div class="masonry" v-if="item">
      <div
        class="brick"
        v-for="(v, k) in item"
        :key="k"
        @click="onClick(v.cat, v.key)"
      >
        <template v-if="v.pics && v.pics.length && (max === 0 || k < max)">
          <img v-lazy="v.pics[0].src" :alt="v.pics[0].alt" :title="v.title" />
          <h2>{{ v.title }}</h2>
        </template>
      </div>
    </div>
    <button class="box-button" @click="loadData" v-if="more">Xem Thêm</button>
  </div>
</template>

<script>
export default {
  name: "masonry",
  props: {
    item: {
      type: Array,
      default: function() {
        return [];
      }
    },
    more: {
      type: Boolean,
      default: false
    },
    moreItems: {
      type: Number,
      default: 10
    },
    max: {
      type: Number,
      default: 0
    }
  },
  methods: {
    onClick(cat, key) {
      this.$emit("onClick", { cat: cat, key: key });
    },
    loadData() {
      let vm = this;
      let cat = vm.item[0].cat || "trending";
      let offset = vm.item.length;
      let limit = vm.moreItems || 50;
      vm.$store.dispatch("News/moreByCat", {
        cat: cat,
        limit: limit,
        offset: offset
      });
    }
  }
};
</script>
