<template>
  <section class="container">
    <div id="filter-wrap">
      <a
        class="filter"
        :class="{ active: params.cat === 'trending' }"
        @click="onFilter('trending')"
        >Xu hướng</a
      >
      <a
        class="filter"
        :class="{ active: params.cat === 'tech' }"
        @click="onFilter('tech')"
        >Công Nghệ</a
      >
      <a
        class="filter"
        :class="{ active: params.cat === 'health' }"
        @click="onFilter('health')"
        >Sức Khoẻ</a
      >
      <a
        class="filter"
        :class="{ active: params.cat === 'foodTravel' }"
        @click="onFilter('foodTravel')"
        >Du Lịch & Ẩm Thực</a
      >
    </div>
  </section>
</template>

<script>
export default {
  name: "Menu",
  props: {
    msg: String
  },
  computed: {
    params: function() {
      return this.$store.getters["News/params"];
    }
  },
  methods: {
    onFilter(key) {
      this.$store.commit("News/SET_CAT", { cat: key });
      this.$store.dispatch("News/filterByCat");
    }
  }
};
</script>

