<template>
  <section class="info-page" v-if="news">
    <div class="cover container">
      <div class="grid">
        <div class="col col-sm">
          <div class="box">
            <div class="box-media" v-if="news.pics && news.pics.length">
              <img :src="news.pics[0].src" :alt="news.pics[0].alt" />
            </div>
            <div class="box-header box-justify">
              <h1>{{ news.title }}</h1>
              <p>{{ news.description }}</p>
            </div>
            <div class="box-body box-justify">
              <p v-for="(c, k) in news.content" :key="k">{{ c }}</p>
            </div>
          </div>
        </div>
        <div class="col col-sm col-4" style="min-width:320px">
          <div class="box" v-if="news.pics && news.pics.length > 1">
            <div class="box-header">
              <h2>Ảnh liên quan</h2>
            </div>
            <div class="box-body">
              <VueSlickCarousel v-bind="slider" class="sliderdrag">
                <div v-for="(p, k) in news.pics" :key="k">
                  <img :src="p.src" :alt="p.alt" />
                  <p>{{ p.alt }}</p>
                </div>
              </VueSlickCarousel>
            </div>
          </div>
          <div class="box" v-if="newses">
            <div class="box-header">
              <h2>Bài viết theo chuyên mục</h2>
            </div>
            <div
              class="box-body"
              v-viewability="{
                padded: 100,
                store: { dispatch: 'News/setup' },
                callback: viewedLoad
              }"
            >
              <Masonry :item="newses" @onClick="goLink" :max="9" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import VueSlickCarousel from "vue-slick-carousel";
import "vue-slick-carousel/dist/vue-slick-carousel.css";
// optional style for arrows & dots
import "@/assets/css/slick.css";

import Masonry from "@/components/Masonry/index.vue";

export default {
  name: "InfoPage",
  components: { Masonry, VueSlickCarousel },
  data() {
    return {
      slider: {
        speed: 300,
        arrows: true,
        dots: false,
        adaptiveHeight: true,
        draggable: true,
        initialSlide: 1
      }
    };
  },
  computed: {
    news: function() {
      return this.$store.getters["News/record"];
    },
    newses: function() {
      return this.$store.getters["News/records"];
    }
  },
  watch: {
    "$route.params": {
      handler(newValue) {
        let vm = this;
        const { cat, key } = newValue;
        vm.$store.dispatch("News/find", { cat: cat, key: key });
      },
      immediate: true
    }
  },
  methods: {
    goLink(opt) {
      this.$router.push({
        name: "Info",
        params: {
          cat: opt.cat,
          key: opt.key
        }
      });
    },
    viewedLoad(store) {
      let vm = this;
      if (!vm.newses.length) {
        vm.$store.dispatch(store.dispatch, vm.news.cat);
      }
    }
  },
  async created() {
    let vm = this;
    let { cat, key } = vm.$route.params;
    vm.$root.$emit("isBack");
    await vm.$store.dispatch("News/find", { cat: cat, key: key });
  }
};
</script>

<style scoped lang="scss">
.info-page .box {
  margin-bottom: 1rem;
}
</style>
