import * as sheetrock from "sheetrock";

function PARSE_GS(rows, one) {
  if (!rows || rows.length === 0) return [];
  let rt = [];

  for (let i = 0; i < rows.length; i++) {
    if (i > 0 || one) {
      let obj = {
        time: rows[i].cellsArray[0] || "",
        link: rows[i].cellsArray[1] || "",
        site: rows[i].cellsArray[2] || "",
        title: rows[i].cellsArray[3] || "",
        key: rows[i].cellsArray[4] || "",
        description: rows[i].cellsArray[5] || "",
        content: JSON.parse(rows[i].cellsArray[6]) || [],
        pics: JSON.parse(rows[i].cellsArray[7]) || [],
        tags: rows[i].cellsArray[8].split(", ") || "",
        cat: rows[i].cellsArray[9] || ""
      };
      rt.push(obj);
    }
  }

  return rt;
}

export default {
  GET_ALL: opt => {
    return new Promise((resolve, reject) => {
      return sheetrock({
        url: opt.url,
        query: "select * order by A desc",
        callback: function(error, options, response) {
          if (error) reject([]);
          let rows = response.rows || [];
          let result = PARSE_GS(rows);
          resolve(result);
        }
      });
    });
  },
  GET_LIMIT: opt => {
    return new Promise((resolve, reject) => {
      return sheetrock({
        url: opt.url,
        query:
          "select * order by A desc limit " +
          (opt.limit || 50) +
          " offset " +
          (opt.offset || 50),
        callback: function(error, options, response) {
          if (error) reject([]);
          let rows = response.rows || [];
          let result = PARSE_GS(rows);
          resolve(result);
        }
      });
    });
  },
  GET: opt => {
    return new Promise((resolve, reject) => {
      return sheetrock({
        url: opt.url,
        query: 'select * where E = "' + opt.key + '"',
        callback: function(error, options, response) {
          if (error) reject({});
          let rows = response.rows || [];
          let result = PARSE_GS(rows, true);
          resolve(result.length ? result[0] : {});
        }
      });
    });
  },
  ADD: item => {
    return new Promise(resolve => {
      setTimeout(() => {
        resolve(item);
      }, 100);
    });
  },
  EDIT: item => {
    return new Promise(resolve => {
      setTimeout(() => {
        resolve(item);
      }, 100);
    });
  },
  REMOVE: item => {
    return new Promise(resolve => {
      setTimeout(() => {
        resolve(item);
      }, 100);
    });
  }
};
