import Vue from "vue";
// directive
import viewability from "@/directives/Viewability";
// component
import VueLazyload from "vue-lazyload";
// main
import App from "./App.vue";
import "./registerServiceWorker";
import router from "./router";
import store from "./store";

Vue.config.productionTip = false;
/**
 * directive
 */
Vue.directive("viewability", viewability);

/**
 * component
 */
Vue.use(VueLazyload, {
  preLoad: 1.5,
  error: "/img/loader2.svg",
  loading: "/img/loader2.svg",
  attempt: 2
});

/**
 * init
 */
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
