<template>
  <div id="search-form">
    <button><img src="/img/icons/search.svg" /></button>
    <input
      type="search"
      placeholder="Search.."
      name="search"
      v-model="search"
      v-on:change="searchTag"
    />
  </div>
</template>

<script>
import _ from "lodash";
export default {
  name: "Search",
  data() {
    return {
      search: ""
    };
  },
  watch: {
    search: _.debounce(function(val) {
      this.$root.$emit("searchChange", val);
    }, 800)
  },
  methods: {
    searchTag(e) {
      let val = e.target.value;
      this.$root.$emit("searchChange", val);
    }
  }
};
</script>

<style scoped lang="scss">
/* Style the search-form field */
#search-form {
  display: table-cell;
  position: relative;
  vertical-align: middle;
  width: 100%;
}
#search-form input[type="search"] {
  display: block;
  padding: 10px 10px 10px 50px;
  font-size: 17px;
  border: 0;
  background: #f1f1f1;
  height: 42px;
  width: 100%;
  border-radius: 24px;
  outline: 0 !important;
}

/* Style the submit button */
#search-form button {
  position: absolute;
  top: 50%;
  left: 1px;
  margin-top: -20px;
  width: 40px;
  height: 40px;
  padding: 10px;
  background: #444;
  border: 0;
  border-radius: 50%;
  outline: none !important;
  cursor: pointer;
}

#search-form button:hover {
  background: #0b7dda;
}
#search-form::after {
  content: "";
